
#app {
  height: auto;
  font-family: 'Open Sans', sans-serif;
  color: #212121;
}
.wrapper {
  width: 80%;
  height: 100%;
  margin: 0 auto;
  text-align: center;
}
hr {
  width: 50%; 
  color: #BDBDBD;
  margin: 0 auto;
}
footer {
  height: 50px;
  padding: 10px;
  background-color: #00BCD4;
  color: #FFFFFF;
}
footer > p {
  margin-top: 15px;
}
.hourly > h1 {
  color: #448AFF;
  font-size: 1rem;
}
.weekly > h1 {
  color: #448AFF;
  font-size: 1.5rem;
}
@media screen and (max-width: 1000px) {
.wrapper {
    width: 100%;
}
}
@media screen and (min-width: 1200px) {
.wrapper {
    width: 1000px;
}
}
@media screen and (max-width: 627px) {
header > h1 {
    font-size: 1rem;
}
footer {
    height: 80px;
}
}
@media screen and (max-width: 750px) {
hr {
    width: 100%;
}
}

